<template>
  <div>
    <van-nav-bar :border="false" @click-left="showProject = true" @click-right="showLanguage = true">
      <template #left>
        <van-icon name="exchange" size="20" /><span style="font-size: 16px; padding-left: 5px;">{{ currentProject.name }}</span>
      </template>
      <template #right>
        <van-icon :name="require('@/assets/icon/language.png')" size="20" />
      </template>
    </van-nav-bar>
    <van-cell-group :border="false" class="my-title">
      <van-cell center>
        <template #icon>
          <van-image round width="4rem" height="4rem" fit="cover" :src="require('@/assets/my_person.png')" />
        </template>
        <template #title>
          <span style="margin-left: 10px;">{{ userInfo.username }}</span>
        </template>
        <template #label>
          <span style="margin-left: 10px;">{{ userInfo.phone }}</span>
        </template>
      </van-cell>
    </van-cell-group>

    <div style="border-radius: 15px;">
      <van-collapse v-model="activeNames" :border="false" style="padding-bottom: 20px;">
        <van-collapse-item :title="$t('person.my_services')" name="0" :border="false" disabled :is-link="false" size="large" class="custom-collapse-item" title-class="custom-collapse-item-title">
          <van-grid :column-num="4" :border="false" class="custom-grid">
            <van-grid-item @click="toRouterUrl('/ticket/list')">
              <van-icon slot="icon" :name="require('@/assets/images/person/icon-grid-2.png')" size="25" />
              <span slot="text">{{ $t('person.my_work_order') }}</span>
            </van-grid-item>
<!--            <van-grid-item @click="toRouterUrl('/update-password')">-->
<!--              <van-icon slot="icon" :name="require('@/assets/images/person/icon-grid-11.png')" size="25" />-->
<!--              <span slot="text">运送</span>-->
<!--            </van-grid-item>-->
            <van-grid-item @click="toRouterUrl('/update-password')">
              <van-icon slot="icon" :name="require('@/assets/images/person/icon-grid-11.png')" size="25" />
              <span slot="text">{{ $t('person.change_password') }}</span>
            </van-grid-item>
            <van-grid-item @click="installDesktop">
              <van-icon slot="icon" :name="require('@/assets/icon/install.png')" size="25" />
              <span slot="text">安装应用</span>
            </van-grid-item>
            <van-grid-item @click="toRouterUrl('/take-photo')">
              <van-icon slot="icon" :name="require('@/assets/images/person/icon-grid-11.png')" size="25" />
              <span slot="text">测试拍照</span>
            </van-grid-item>
            <van-grid-item @click="scan">
              <van-icon slot="icon" :name="require('@/assets/images/person/icon-grid-11.png')" size="25" />
              <span slot="text">测试扫一扫</span>
            </van-grid-item>
            <van-grid-item @click="logout">
              <van-icon slot="icon" :name="require('@/assets/icon/logout.png')" size="25" />
              <span slot="text">{{ $t('person.logout') }}</span>
            </van-grid-item>
          </van-grid>
        </van-collapse-item>
      </van-collapse>
    </div>
    <van-action-sheet v-model="showLanguage" :actions="listLanguage" cancel-text="取消" close-on-click-action @cancel="showLanguage = false" @select="changeLanguage" />
    <van-action-sheet v-model="showProject" :actions="listProject" cancel-text="取消" close-on-click-action @cancel="showProject = false" @select="changeProject" />
  </div>
</template>

<script>
import {menuList} from '@/utils/menu'
import eventBus from '@/utils/eventBus'

export default {
  name: 'PersonIndex',
  data () {
    return {
      userInfo: this.$store.getters.getUserInfo,
      language: this.$store.getters.getLanguage,
      currentProject: this.$store.getters.getCurrentProject,
      listProject: this.$store.getters.getListProject,
      showLanguage: false,
      listLanguage: [{ name: 'English', code: 'en', disabled: false }, { name: '简体中文', code: 'zh', disabled: false }],
      showProject: false,
      menuList,
      activeNames: ['0', '1']
    }
  },
  mounted() {
    eventBus.$off('ScanResult')
    eventBus.$on('ScanResult', function(data) {
      this.saveScanResult(data)
    }.bind(this))

    this.listLanguage.forEach(it => {
      if (it.code === this.language) {
        it.disabled = true
      }
    })
  },
  methods: {
    toRouterUrl(url) {
      this.$utils.routerUrl(url)
    },
    logout() {
      this.$dialog.confirm({
        title: '退出提示',
        message: '确定要退出登录吗？'
      }).then(() => {
        this.$common.removeToken()
        this.$router.replace('/')
      }).catch(() => {
      })
    },
    saveScanResult(data) {
      alert(data)
    },
    installDesktop() {
      this.$dialog.confirm({
        title: '现在，您可以添加此程序',
        message: '放在桌面上你喜欢的位置，可以快速访问应用',
        confirmButtonText: '安装'
      }).then(() => {
        try {
          window.deferredPrompt.prompt()
          window.deferredPrompt.userChoice.then((choiceResult) => {
            if (choiceResult.outcome === 'accepted') {
              this.$toast('安装成功')
            } else {
              this.$toast('取消安装')
            }
            window.deferredPrompt = null
          })
        } catch (e) {
          console.log(e)
        }
      }).catch(() => {
        this.$toast('取消安装')
      })
    },
    changeLanguage(item) {
      this.$dialog.confirm({
        title: '系统提示',
        message: `语言确定要切换成${item.name}吗？`
      }).then(async () => {
        await this.$post('/sys/language/change', { language: item.code })
        await this.$store.dispatch('setLanguage', item.code)
        this.$toast('Switch Language Success')
        location.reload()
      }).catch(() => {
      })
    },
    changeProject(item) {
      this.showProject = false
      this.$dialog.confirm({
        title: '系统提示',
        message: '确定要切换项目吗？'
      }).then(async () => {
        // 改变项目需要重新登录
        const res = await this.$post('/sys/config/change', { sysCode: item.code })
        await this.$store.dispatch('setCurrentProject', res.currentProject)
        await this.$store.dispatch('setListProject', res.listProject)
        location.reload()
      }).catch(() => {
      })
    },
    scan() {
      this.$utils.scan()
    }
  }
}
</script>

<style scoped>
  .my-title {
    margin-bottom: 15px;
    height: 100px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .custom-collapse-item {
    margin: 15px;
    overflow: hidden;
    border-radius: 8px;
  }
</style>
<style>
.custom-collapse-item-title {
  font-weight: bold;
  color: #323233
}
</style>
